import React from "react";
import { Card, Container } from "react-bootstrap";
import HoverCloseCard from "./HoverCloseCard";
import { getBookByISBN } from "../books_api";
import '../components/styles/library.scss';

export default function Library({lib, showBookPanel, selected}){
    const [books, setBooks] = React.useState([]);
    const [status, setStatus] = React.useState(true);
    const [tbrSize, setTBRSize] = React.useState(4);
    const [readSize, setReadSize] = React.useState(4);

    let renderedBooks;

    function randomizeBooks(){
            renderedBooks = [];
            let numRand = 0;
            lib.cr.forEach((ele) => renderedBooks.push(ele));
            while(numRand < lib.read.length){
                let randEle = lib.read[Math.floor(Math.random() * lib.read.length)];
                if(!renderedBooks.includes(randEle)){
                    renderedBooks.push(randEle)
                    numRand++;
                }
            }
                numRand = 0;
                while(numRand < lib.tbr.length){
                    let randEle = lib.tbr[Math.floor(Math.random() * lib.tbr.length)];
                    if(!renderedBooks.includes(randEle)){
                        renderedBooks.push(randEle)
                        numRand++;
                    }
                }
    }

    function sizeToggle(obj){
        // obj:
        // type: "read", "tbr"
        // size: int
        const length = obj.size;
        const sec = obj.type;

        if(sec === "read"){
            if(length === lib.read.length){
                setReadSize(4);
            } else {
                setReadSize(lib.read.length);
            }
        } else {
            if(length === lib.tbr.length){
                setTBRSize(4);
            } else {
                setTBRSize(lib.tbr.length);
            }
        }

    }

    React.useEffect(() => {
            // determine which books to render
            // separate ISBNs into respective piles (read, tbr, cr) -> already done in lib prop
            // select 3 (at most) from cr, 4 random from read, 4 random from tbr (11 total)
            // compile into new array (use in place of old lib.old array)
            if(localStorage.length-2 <= lib.full.length){
                const fetchData = async () => {
                    try {
                    const fetchPromises = lib.full.filter(isbn => !JSON.parse(localStorage.getItem(isbn))).map(isbn => getBookByISBN(isbn));
                    const booksData = await Promise.all(fetchPromises);

                    booksData
                    .filter((book) => book !== undefined || book !== null)
                    .map((book) => {
                        return{
                            ...book,
                            favorite: lib.favorites.includes(book.volumeInfo.industryIdentifiers[0].identifier) || lib.favorites.includes(book.volumeInfo.industryIdentifiers[1].identifier),
                            read: lib.read.includes(book.volumeInfo.industryIdentifiers[0].identifier) || lib.read.includes(book.volumeInfo.industryIdentifiers[1].identifier),
                            tbr: lib.tbr.includes(book.volumeInfo.industryIdentifiers[0].identifier) || lib.tbr.includes(book.volumeInfo.industryIdentifiers[1].identifier),
                            cr: lib.cr.includes(book.volumeInfo.industryIdentifiers[0].identifier) || lib.cr.includes(book.volumeInfo.industryIdentifiers[1].identifier),
                            dateAdded: new Date().toLocaleDateString(),
                            dateStarted: "",
                            dateFinished: "",
                            currPage: 0,
                            progress: 0.00
                        }
                    })
                    .map((book) => {
                        if(book.volumeInfo.industryIdentifiers.length === 1){
                            localStorage.setItem(book.volumeInfo.industryIdentifiers[0].identifier, JSON.stringify(book))
                        }
                        else if(book.volumeInfo.industryIdentifiers.length > 1){
                            if(book.volumeInfo.industryIdentifiers[0].type === "ISBN_13"){
                                localStorage.setItem(book.volumeInfo.industryIdentifiers[0].identifier, JSON.stringify(book))
                            } else {
                                localStorage.setItem(book.volumeInfo.industryIdentifiers[1].identifier, JSON.stringify(book))
                            }
                        }
                        return;
                    })
                    } catch (error) {
                        console.error('Error here:', error);
                        setStatus(false)
                    }
                };

                fetchData();
            }

            let render;
            randomizeBooks();
            render = renderedBooks.map((isbn) => {
                return JSON.parse(localStorage.getItem(isbn));
            })
            setBooks(render);
            
        }, [lib.full, lib.cr, lib.read, lib.tbr, renderedBooks])

    return(
        <Container className='mt-5 bg-dark text-light' style={{zIndex: '-2', textAlign: 'left'}}>
            <h1>Currently Reading</h1>
            <Container className="d-flex flex-wrap pr-2 justify-content-start" style={{paddingLeft: '0'}}>
                {books.map((book) =>
                    {
                        return book !== null && book.cr ? 
                        <HoverCloseCard key={book.id}
                        isbns={book.volumeInfo.industryIdentifiers} 
                        img={book.volumeInfo.imageLinks.thumbnail} 
                        className="mb-0 mx-1 d-inline-block book-cover"
                        showBookPanel={showBookPanel}
                        selected={selected}
                        />

                        : ""
                    }
                ).filter((comp) => comp !== "").slice(0, readSize)}  
              
          </Container>

             <div className="d-flex">
                <h1>Read</h1>
                {books.filter((book) => book !== null && book.read).length > 4 ?  <p className="size-text mt-3 px-2" onClick={() => sizeToggle({type: "read", size: readSize})}>{readSize === lib.read.length ? "Collapse" : "See all"}</p> : ""}
             </div>
             <Container fluid className="d-flex pr-2 flex-wrap justify-content-start mt-2" style={{paddingLeft: '0'}}>
                 {books.map((book) =>
                    {
                        return book !== null && book.read ? 
                        <HoverCloseCard key={book.id}
                        isbns={book.volumeInfo.industryIdentifiers} 
                        img={book.volumeInfo.imageLinks.thumbnail} 
                        className="mb-0 mx-1 d-inline-block book-cover"
                        showBookPanel={showBookPanel}
                        selected={selected}
                        />

                        : ""
                    }
                ).filter((comp) => comp !== "").slice(0, readSize)}
            </Container>

            <div className="d-flex">
                <h1>To-Read</h1>
                {books.filter((book) => book !== null && book.tbr).length > 4 ? <p className="size-text mt-3 px-2" onClick={() => sizeToggle({type: "tbr", size: tbrSize})}>{tbrSize === lib.tbr.length ? "Collapse" : "See all"}</p> : ""}
             </div> 
                <Container className="d-flex pr-2 justify-content-start" style={{paddingLeft: '0'}}>
                {books.map((book) =>
                    {
                        return book !== null && book.tbr ? 
                        <HoverCloseCard key={book.id}
                        isbns={book.volumeInfo.industryIdentifiers} 
                        img={book.volumeInfo.imageLinks.thumbnail} 
                        className="mb-0 mx-1 d-inline-block book-cover"
                        showBookPanel={showBookPanel}
                        selected={selected}
                        />
                        : ""
                    }
                ).filter((comp) => comp !== "").slice(0, tbrSize)}
            </Container>
        </Container>

    )
}