import React from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { Container, Button, Row, Form } from "react-bootstrap";
import './styles/bookinfopanel.scss'

export default function BookInfoPanel({bookInfo}){
    const [dateStarted, setDateStarted] = React.useState(bookInfo.dateStarted);
    const [dateStartText, setDateStartText] = React.useState(bookInfo.dateStarted)
    const [dateFinishedText, setDateFinishedText] = React.useState(bookInfo.dateFinished)
    const [dateFinished, setDateFinished] = React.useState(bookInfo.dateFinished);
    const [editDateStart, setEditDateStart] = React.useState(false);
    const [editDateFinished, setEditDateFinished] = React.useState(false);
    const [notes, setNotes] = React.useState("");
    const [showUpdateProgress, setShowUpdateProgress] = React.useState(false);
    const [progress, setProgress] = React.useState({
        "currPage": bookInfo.currPage,
        "pageCount": bookInfo.volumeInfo.pageCount
    })
    const [showStatusChange, setShowStatusChange] = React.useState(false);
    const [showEditionPanel, setShowEditionPanel] = React.useState(false);
    const [bookFormat, setBookFormat] = React.useState(bookInfo.format !== null || bookInfo.format !== undefined ? bookInfo.format : "book");
    const [customImg, setCustomImg] = React.useState("");


    React.useEffect(() => {
        setDateStartText(bookInfo.dateStarted);
        setDateFinishedText(bookInfo.dateFinished);
        setDateStarted(bookInfo.dateStarted);
        setDateFinished(bookInfo.dateFinished);
        if(bookInfo.notes !== undefined && bookInfo.notes !== null) setNotes(bookInfo.notes);
        else setNotes("");
        setProgress({
            "currPage": bookInfo.currPage,
            "pageCount": bookInfo.volumeInfo.pageCount
        })
        setShowUpdateProgress(false);
        setBookFormat(bookInfo.format !== null || bookInfo.format !== undefined ? bookInfo.format : "book");
        setShowEditionPanel(false);
        setCustomImg(bookInfo.volumeInfo.imageLinks.thumbnail);
    }, [bookInfo])

    const isbnInStorage = () => {
        if(bookInfo.volumeInfo.industryIdentifiers){
            if(bookInfo.volumeInfo.industryIdentifiers.length > 1){
                if(JSON.parse(localStorage.getItem(bookInfo.volumeInfo.industryIdentifiers[0].identifier))){
                    return bookInfo.volumeInfo.industryIdentifiers[0].identifier;
                } else return bookInfo.volumeInfo.industryIdentifiers[1].identifier;
            } else {
                return bookInfo.volumeInfo.industryIdentifiers[0].identifier;
            }
        } else return undefined;
    }

    const startDatePicker = () => {
        let selected = dateStarted !== "" ? Date.parse(dateStarted) : new Date();
        return (
            <DatePicker dateFormat="MM/dd/yyyy" selected={selected} onChange={(date) => {
                let newVal = JSON.parse(localStorage.getItem(isbnInStorage()));
                newVal.dateStarted = date.toLocaleDateString()
                localStorage.setItem(isbnInStorage(), JSON.stringify(newVal));
                setDateStarted(date.toJSON())
                setEditDateStart(false);
            }}/>
        )
     }

     const finishDatePicker = () => {
        let selected = dateFinished !== "" ? Date.parse(dateFinished) : new Date();
        return (
            <DatePicker dateFormat="MM/dd/yyyy" selected={selected} onChange={(date) => {
                let newVal = JSON.parse(localStorage.getItem(isbnInStorage()));
                newVal.dateFinished = date.toLocaleDateString()
                localStorage.setItem(isbnInStorage(), JSON.stringify(newVal));
                setDateFinished(date.toJSON())
                setEditDateFinished(false);
            }}/>
        )
     }

     const bookStartedText = () => {
        if(bookInfo.cr || bookInfo.read){
            return dateStartText !== "" ?
            <div className="d-flex">
                {!editDateStart ? <h6>Date Started: {dateStartText}</h6> : startDatePicker()}
                <h6 className="editparam fw-bold" onClick={() => {
                    setEditDateStart(true);
                    }} style={{marginLeft: '8px'}}>Edit</h6>
            </div> 
              :
            <div className="d-flex">
                {!editDateStart ? <h6><i>No start date set</i></h6> : startDatePicker()}
                <h6 className="editparam fw-bold" onClick={() => {
                    setEditDateStart(true);
                    }} style={{marginLeft: '8px'}}>Edit</h6>
            </div>
        }
    }

    const bookFinishedText = () => {
        if(bookInfo.read){
            return dateFinishedText !== "" ?
            <div className="d-flex">
                {!editDateFinished ? <h6>Date Finished: {dateFinishedText}</h6> : finishDatePicker()}
                <h6 className="editparam fw-bold" onClick={() => {
                    setEditDateFinished(true);
                    }} style={{marginLeft: '8px'}}>Edit</h6>
            </div> 
              :
            <div className="d-flex">
                {!editDateFinished ? <h6><i>No finished date set</i></h6> : finishDatePicker()}
                <h6 className="editparam fw-bold" onClick={() => {
                    setEditDateFinished(true);
                    }} style={{marginLeft: '8px'}}>Edit</h6>
            </div>
        }
    }

    const datesBetween = (day1, day2) => {
        return Math.abs(Math.ceil((new Date(day2) - new Date(day1)) / (1000 * 60 * 60 * 24)));
    }

    const datesReading = () => {
        let d1;
        let d2;
        if(bookInfo.dateStarted !== ""){
            d1 = new Date(dateStarted);
            if(bookInfo.dateFinished !== "") {
                d2 = new Date(dateFinished);
            }
            else {
                d2 = new Date();
            }
            return datesBetween(d2, d1);
        }
    }

    const datesReadingText = () => {
        let days = datesReading();
        if(bookInfo.format === 'book' || bookInfo.format === undefined){
            if(days > 1) return <h6>Days Reading: {days} days</h6>;
            else if (days === 1) return <h6>Days Reading: {days} day</h6>
            else return "";
        } else {
            if(days > 1) return <h6>Days Listening: {days} days</h6>;
            else if (days === 1) return <h6>Days Listening: {days} day</h6>
            else return "";
        }
    }

    const bookProgress = () => {
        let progNum = (progress.currPage/progress.pageCount)*100 === Infinity ? 0 : (progress.currPage/progress.pageCount)*100;

        return(
            <div>
                <div className="d-flex">
                    <h5 className="text-light">Progress: {Math.round(progNum)}%</h5>
                    <h5 className="editparam fw-bold" onClick={() => {
                        setShowUpdateProgress(!showUpdateProgress)
                        }} style={{marginLeft: '8px'}}>{showUpdateProgress ? "Hide" : "Update"}</h5>
                </div> 
                {showUpdateProgress ? 
                <Container>
                    <Row>
                        <div className="d-flex align-items-center">
                            {bookInfo.format === "book" || bookInfo.format === undefined ? <h6 className="mb-0">Current Page:</h6> : <h6 className="mb-0">Time Listened:</h6>}
                            <form onSubmit={handleUpdateProgressSubmit}>
                                <input
                                    type="text"
                                    placeholder={bookInfo.format === "book" || bookInfo.format === undefined ? progress.currPage : progress.currPage + " minutes"}
                                    className="text-light bg-dark rounded"
                                    style={{border: '0'}}
                                    name="currPage"
                                    onChange={handleUpdateProgress}
                                />
                            </form>
                        </div>
                    </Row>
                    <Row>
                    <div className="d-flex align-items-center mt-1">
                    {bookInfo.format === "book" || bookInfo.format === undefined ? <h6 className="mb-0">Total Pages:</h6> : <h6 className="mb-0">Total Time:</h6>}
                            <form onSubmit={handleUpdateProgressSubmit}>
                                <input
                                    type="text"
                                    placeholder={bookInfo.format === "book" || bookInfo.format === undefined ? progress.pageCount : progress.pageCount + " minutes"}
                                    className="text-light bg-dark rounded"
                                    style={{border: '0'}}
                                    name="pageCount"
                                    onChange={handleUpdateProgress}
                                />
                            </form>
                        </div>
                    </Row>
                </Container> : ""}
            </div>
        )
    }

    const pagePaceText = () => {
        let currPage = progress.currPage;
        let daysReading = datesReading();
        let val;
        let text;

        if((progress.currPage/daysReading === Infinity || progress.pageCount/daysReading === Infinity)
        || isNaN(progress.currPage/daysReading) || isNaN(progress.pageCount/daysReading)){
            val = 0;
        } else {
            if (bookInfo.cr) val = Math.ceil(currPage/daysReading);
            else if (bookInfo.read) val = Math.ceil(progress.pageCount/daysReading);
            else val = 0
        }

        if(bookInfo.format === undefined || bookInfo.format === 'read'){
            text = val + " pages per day";
        } else {
            text = val + " minutes per day"
        }

        return <h6>Average Pace: {text}</h6>
    }
    function handleNotesChange(event) {
                setNotes(event.target.value);
         }

    function handleNotesSubmit(event){
        event.preventDefault();
        let obj = JSON.parse(localStorage.getItem(isbnInStorage()));
        obj.notes = notes;
        localStorage.setItem(isbnInStorage(), JSON.stringify(obj));
    }

    function handleUpdateProgress(event){
        const {name, value} = event.target
        let converted = !isNaN(parseInt(value));
        if(converted) {
            setProgress(prevProgress => {
                return {
                    ...prevProgress,
                    [name]: value
                }
            })
        }
    }

    function handleUpdateProgressSubmit(event){
        let obj = JSON.parse(localStorage.getItem(isbnInStorage()));
        obj.currPage = progress.currPage;
        obj.volumeInfo.pageCount = progress.pageCount;
        if(progress.currPage/progress.pageCount >= 1 && progress.currPage/progress.pageCount !== Infinity) {
            obj.cr = false;
            obj.read = true;
        }
        localStorage.setItem(isbnInStorage(), JSON.stringify(obj));
    }

    const statusText = () => {
        let label;
        let status;
        if(bookInfo.cr){
            label = "Currently Reading";
            status = 'cr';
        }
        else if(bookInfo.read){
            label = "Read";
            status = 'read';
        }
        else{
            label = "To-Read";
            status = 'tbr';
        }

        return(
            <div>
                <h4 className="hoverable-text" onClick={() => setShowStatusChange(!showStatusChange)}>{label}</h4>
                {showStatusChange ? 
                    <Container>
                        {!bookInfo.cr ? 
                        <Row className="hoverable-row">
                            <h4 className="hoverable-text" onClick={() => handleStatusClick(status, 'cr')}>Currently Reading</h4>
                        </Row> : ""}
                        {!bookInfo.read ? 
                        <Row className="hoverable-row">
                            <h4 className="hoverable-text" onClick={() => handleStatusClick(status, 'read')}>Read</h4>
                        </Row> : ""}
                        {!bookInfo.tbr ? 
                        <Row className="hoverable-row">
                            <h4 className="hoverable-text" onClick={() => handleStatusClick(status, 'tbr')}>To-Read</h4>
                        </Row> : ""}
                    </Container>
                : ""}
            </div>
        )
    }

    const editionPanel = () => {
        return (
            <Container>
                <Row className="mv-1">
                    <div className="d-flex align-items-center">
                            <h6>Customize cover: </h6>
                            <Form onSubmit={() => {
                                let obj = JSON.parse(localStorage.getItem(isbnInStorage()));
                                obj.volumeInfo.imageLinks.thumbnail = customImg;
                                localStorage.setItem(isbnInStorage(), JSON.stringify(obj));
                            }}>
                                <Form.Control 
                                    size="sm"
                                    className="bg-dark text-light"
                                    // style={{width: '35%'}}
                                    placeholder='Enter Image URL'
                                    value={customImg}
                                    onChange={(event) => {
                                        setCustomImg(event.target.value);
                                    }
                                }
                                />
                            </Form>
                    </div>
                </Row>
                <Row className="mt-2 mb-1">
                    <div className="d-flex align-items-center">
                        <h6>Book format: </h6>
                        <Form.Select
                         size="sm"
                         className="bg-dark text-light"
                         style={{width: '25%'}}
                         value={bookFormat}
                         name="bookFormat"
                         onChange={handleBookFormat}
                         >
                            <option value="book">Book</option>
                            <option value="audio">Audiobook</option>
                        </Form.Select>
                    </div>
                </Row>
            </Container>
        )
    }

    function handleBookFormat(event){
        const {value} = event.target;
        setBookFormat(value);
        let obj = JSON.parse(localStorage.getItem(isbnInStorage()));
        obj.format = value;
        localStorage.setItem(isbnInStorage(), JSON.stringify(obj));
    }

    function handleStatusClick(currStatus, newStatus){
        let obj = JSON.parse(localStorage.getItem(isbnInStorage()));
        obj[currStatus] = false;
        obj[newStatus] = true;
        // console.log(obj);
        localStorage.setItem(isbnInStorage(), JSON.stringify(obj));
        // changeBookStatus(isbnInStorage(), currStatus, newStatus);
    }
    return(
        <Container className="mt-5 text-light">
            <h1>{bookInfo.volumeInfo.title} <i>({bookInfo.volumeInfo.publishedDate.slice(0, 4)})</i></h1>
            <h3>{bookInfo.volumeInfo.authors[0]}</h3>
            <h6 className="mb-0 hoverable-text" onClick={() => setShowEditionPanel(!showEditionPanel)}><i>Change cover or format?</i></h6>
            {showEditionPanel ? editionPanel() : ""}
            <h6>Added to library: {bookInfo.dateAdded}</h6>
            {statusText()}
            {bookStartedText()}
            {bookFinishedText()}
            {!bookInfo.tbr ? pagePaceText() : ""}


            {bookInfo.cr || bookInfo.read ? datesReadingText() : ""}
            {bookInfo.cr ? bookProgress() : ""}


            <form className='bg-dark text-light d-flex flex-column mt-2' onSubmit={handleNotesSubmit}>
                <h6>Notes:</h6>
                <textarea className='bg-dark text-light rounded' onChange={handleNotesChange} value={notes}/>
                {notes !== bookInfo.notes ? <Button className="mt-1 "onClick={handleNotesSubmit}>Save</Button> : <Button size='sm' className="mt-1 bg-black "onClick={handleNotesSubmit}>Saved</Button>}
            </form>

        </Container>
    )
}