import React from 'react';
import Home from './components/Home';
import WelcomeModal from './components/WelcomeModal';
import 'bootstrap/dist/css/bootstrap.min.css'
import './App.scss';

function App() {
  const [showWelcome, setShowWelcome] = React.useState(false);
  React.useEffect(() => {
    if(!JSON.parse(localStorage.getItem("loaded"))){
      console.log("Welcome");
      localStorage.setItem("loaded", JSON.stringify(1));
      setShowWelcome(true);
    } 
    else {
      if(JSON.parse(localStorage.getItem("loaded") <= 1)){
        let obj = JSON.parse(localStorage.getItem("loaded"));
        obj += 1;
        console.log(obj);
        localStorage.setItem("loaded", JSON.stringify(obj));
        setShowWelcome(true);
      }
      else {
        // Change back to false
        setShowWelcome(false)
      }
    }

  }, [])

  return (
    <main>
      <Home />
      <WelcomeModal show={showWelcome} onHide={setShowWelcome} />
    </main>
  );
}

export default App;
