export let books_key = 'AIzaSyCYweG_l5ommTDyNc72_teCmwDgmNINA60';

export function generateSearchURI(search){
    let str = "https://www.googleapis.com/books/v1/volumes?q="
    if(search.term.trim() !== ""){
        let term = search.term.replaceAll(" ", "_");
        str += term;
    }
    if(search.isbn.trim() !== ""){
        let isbn = search.isbn.trim();
        str += "isbn:" + isbn
    }
    str += "&maxResults=30&printType=books&key="+books_key;
    return str;
}

function fetchByISBN(isbn) {
    return fetch("https://www.googleapis.com/books/v1/volumes?q=isbn:" + isbn + "&printType=books&key=AIzaSyCYweG_l5ommTDyNc72_teCmwDgmNINA60")
        .then(res => res.json())
        .then(data => data.items);
}

export function getBookByISBN(isbn) {
    return fetchByISBN(isbn)
      .then(result => {
        // Assuming getByISBN returns an array of books, you might want to handle the data accordingly
        const book = result && result.length > 0 ? result[0] : null;
        return book;
      })
      .catch(error => {
        // Handle errors if any
        console.error('Error:', error);
        throw error; // Re-throw the error to propagate it to the next error handler if needed
      });
}


