import React from "react";
import { Modal } from "react-bootstrap";

export default function HelpModal({show, onHide}){
    return(
        <Modal size='xl' show={show} onHide={() => onHide(false)}>
            <Modal.Header>
                <Modal.Title>Help Menu</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <p>Can't find something? Something confusing? Something doesn't look right?
                This menu is designed to help you get started setting up your library, troubleshooting common issues, or providing a little extra detail on the app's functionality!
                If you find a bug that is not documented here or is impacting your use of the app, please contact me at <a href='mailto:masonlanger00@gmail.com?subject=Reading Library Bug Report'>masonlanger00@gmail.com</a>.</p>
                <h3>Getting Started</h3>
                <h4>Adding a Book</h4>
                <p>In order to get started cataloging your library and tracking your progress, you have to add your books! Begin by searching for your book in the top-right corner: the best
                    results can be found by getting specific as possible (i.e. full title and full author name). Any other detail, such as publisher or ISBN, may be included, but can potentially
                    affect the ability to find your book. Hit enter and the web app will search for your book based on the search term you gave.
                </p>
                <p>
                    After searching, the top four most relevant books will be returned. Click on the title of the book you want to add to your library and a new menu will be opened.
                    This menu includes information such as the publisher, date published, and the number of pages for the given edition of the book. A select group of other editions of
                    the same book may be found at the bottom–these editions are curated based on the search criteria you gave (the more specific you are with your search, the more editions will typically be shown).
                </p>

                <p>
                    If you have already added books to your library, you might find "In Library" to the left side of this menu if it is a book edition you have already added. If not, then you should be able to find
                    the "Add to library" button at the left of the menu. Clicking on this button will open a panel with three options: "To-Read", "Read", and "Currently Reading."
                    Clicking on any of these options adds the given book edition to your library under that category. Refresh to see the changes to your library!
                </p>

                <h4>Removing a Book</h4>
                <p>
                    If, for one reason or another, you would like to remove a book from your library, simply hover over (or click on mobile) the book you want to remove and click the <b>X</b> that appears next to it. After refreshing, you should find your library is without that book.
                </p>
                <p><i><b>Developer notes: </b>This web app utilizes Google Books API for lots of its features, but particularly the search function. While the API exposes a lot of great information,
                        it does not make it easy to access different versions of the same book. Additionally, the vast amount of books it exposes means that some niche volumes may supersede popular books or editions,
                        especially when search terms–which is why specificity is stressed above. How to update certain details about the edition you added to your library, such as the number of pages, format,
                        or the edition's cover will be described below.
                </i></p>
                <h4>Updating Your Edition</h4>
                <p>If you couldn't find your specific edition on your initial search, you can still update certain details about the edition you choose to better reflect your book!
                    To begin, select the book from your library that you want to update by clicking on it–this should open more information about your book on the right side of the page.
                    This is where you can change details about the selected edition:
                </p>
                <p>
                    <b>Important note: </b> To deselect a book and hide the information panel, simply click on the selected book (which is indicated by a slightly darkened cover to the left) to make the information panel go away.
                </p>
                <ul>
                    <li>
                        <h5>Book Cover: </h5>
                        <p>Click on the text <i>"Change cover or format"</i> towards the top the right panel, which will reveal the options "Customize cover" and "Book format."
                            The field to the right of "Customize cover" contains the current source URL for the current cover in your library. Simply copy and paste the image URL of
                            your preferred cover into this field, hit your "Enter" key, and your newly updated cover will appear in your library!
                        </p>
                    </li>
                    <li>
                        <h5>Book Format: </h5>
                        <p>Click on the text <i>"Change cover or format"</i> towards the top the right panel, which will reveal the options "Customize cover" and "Book format."
                            The field to the right of "Book format" allows you to select between two options–"Book" pertaining to physical books or ebooks, or books with page numbers in general, and "Audiobook," which pertains to audiobooks.
                            Newly added books default to the "Book" option by default.
                        </p>
                    </li>
                    <li>
                        <h5>Total Pages: </h5>
                        <p>Click on the <b>"Update"</b> text to the right of the "Progress" status towards the middle of the panel.
                            This will reveal two fields, "Current Page" and "Total Pages." If you want to update the total number of pages for your
                            edition, simply enter the new amount of pages in the "Total Pages" field and hit your "Enter" key. 
                        </p>
                    </li>
                    <li>
                        <h5>Book Status: </h5>
                        <p>
                            The initial status you chose for your book can be changed in the right information panel as well.
                            For a book that is under "To-Read," click on the text "To-Read" in the middle of the information panel–between the date the book was added 
                            to your library and "Notes." This will open up two options underneath the "To-Read" text: "Currently Reading" and "Read."
                            Clicking on either of these options will update the book's status accordingly. Books in other categories can be changed accordingly. The page will need to
                            be refreshed to reflect changes. 
                            <i> Note: A book in the "Currently Reading" category will automatically update to "Read" if its progress surpasses 100%, setting its "Date Finished" field as the current day.</i> 
                        </p>
                    </li>
                </ul>
                <h4>Keeping Track of Your Progress</h4>
                <p>The main point of this app is to track your reading habits and stats about certain books that you have read. In order to do this, you
                    have to provide some information yourself! The following will detail how you can achieve this by setting data such as "Date Started," "Date Finished," and "Current Page."
                </p>
                <ul>
                    <li>
                        <h5>Date Started: </h5>
                        <p>
                            For a book that is under the categories "Currently Reading" and "Read", you will see a field "Date Started" in the middle
                            of the information panel for a given book. For a book that is newly added to either category, you will see "No start date set."
                            This can be changed by clicking the <b>"Edit"</b> text, which will open a date picker where you can select the date you started for the book.
                            If there is a need to change this in the future, follow the same process as above.
                        </p>
                    </li>
                    <li>
                        <h5>Date Finished: </h5>
                        <p>
                            For a book that is under the category "Read", you will see a field "Date Finished" in the middle
                            of the information panel for a given book. For a book that is newly added to the category, you may see "No finished date set."
                            This can be changed by clicking the <b>"Edit"</b> text, which will open a date picker where you can select the date you finished the book.
                            If there is a need to change this in the future, follow the same process as above. As noted above, this field will automatically be populated if
                            the progress surpassed 100%.
                        </p>
                    </li>
                    <li>
                        <h5>Current Page: </h5>
                        <p>
                            For a book that is under the category "Currently Reading", can update the page you are currently on underneath the "Progress" status text, underneath the
                            "Date Started" and "Days Reading" text. Click the <b>"Update"</b> text next to the progress indicator to open up a new panel with the field "Current Page."
                            Enter the page you are on in this field and hit the "Enter" key, which will save your new progress.
                        </p>
                    </li>
                    <li>
                        <h5>Notes:</h5>
                        <p>
                            For any book, you can add notes! This is found at the bottom of the information panel, where you can enter reminders, quotes, impressions, or anything else
                            you find appropriate. Be sure to click on the "Save" button underneath the text field to save the notes alongside your edition!
                        </p>
                    </li>
                </ul>
                
            </Modal.Body>
        </Modal>
    )
}