import React from "react";
import { getBookByISBN } from "../books_api";
import { Container } from "react-bootstrap";

export default function ModalEditionImagesContainer({otherEditions, changeModalBook }){
    const [books, setBooks] = React.useState([]);

    React.useEffect(() => {
        const fetchData = async () => {
            try {
                const fetchPromises = otherEditions.map(isbn => getBookByISBN(isbn));
                const booksData = await Promise.all(fetchPromises);
                setBooks(booksData);
            } catch (error) {
                console.error('Error here:', error);
            }
    }
    fetchData();
} ,[]);

    let imgArr = books.length > 0 ? books.filter(book => book !== null).map((book) => {
        if(book.volumeInfo.imageLinks) return <img key={book.id} src={book.volumeInfo.imageLinks.thumbnail}
            onClick={() => changeModalBook(book)}
            alt={book.volumeInfo.title}
        />;
    }) : [];

    return(
        <Container>
            {imgArr}
        </Container>
    )
}