import React from "react";
import { Container, Card } from "react-bootstrap";
import BookModal from "../BookModal";
import HelpModal from "./HelpModal";
import { BooksHashMap } from '../BooksHashMap';
import { generateSearchURI } from "../books_api";
import '../components/styles/searchbar.scss'

export default function SearchBar({addBook}){
    const [search, setSearch] = React.useState({
        "term": "",
        "isbn": ""
    });
    const [searchURI, setSearchURI] = React.useState("https://www.googleapis.com/books/v1/volumes?q=printType=books&key=AIzaSyCYweG_l5ommTDyNc72_teCmwDgmNINA60");

    const [bookResponse, setBookResponse] = React.useState([]);
    const [hidden, setHidden] = React.useState(true);
    const [showAddBook, setShowAddBook] = React.useState(false);
    const [showModal, setShowModal] = React.useState(false);
    const [modalData, setModalData] = React.useState({volumeInfo: {
        title: "",
        authors: [
            ""
        ],
        description: "",
        imageLinks: {
            thumbnail: ""
        },
        industryIndentifiers: [
            {
                type: "",
                identifier: "",
            },
            {
                type: "",
                identifier: "",
            }
        ]
        }
    });
    const [showHelp, setShowHelp] = React.useState(false);


    let hm = new BooksHashMap(32);
    
    function handleChange(event){
        const {name, value} = event.target
        setSearch((prevSearch) => {
            return {
                ...prevSearch,
                [name]: value
            }
        })

        if(search.term.trim() !== "") {
            hm.clear();
            setHidden(true);
        }
    }

    function handleSubmit(event){
        event.preventDefault();
        setSearchURI(generateSearchURI(search));
        setHidden(false);
    }
    
    function convertResponseHashtable(data){
        if(data === undefined){
            return undefined;
        } else {
            data.forEach(ele => hm.put(ele));
            let booksData = hm.convertToJSONArray();
            let sorted = hm.insertionSort(booksData);
            return sorted;
        }
    }
    

    React.useEffect(() => {
        if(searchURI !== "https://www.googleapis.com/books/v1/volumes?q=printType=books&key=AIzaSyCYweG_l5ommTDyNc72_teCmwDgmNINA60"){
            try {
                fetch(searchURI)
                .then(res => res.json())
                .then(data => data.items)
                .then(data => convertResponseHashtable(data))
                .then(data => setBookResponse(data))
            } catch (error){
                console.error('Error here:', error);
            }
        }
    }, [searchURI]);

    let resSuccess = bookResponse !== undefined

    let cardArr = resSuccess ? bookResponse.filter((book) => book.volumeInfo.imageLinks).map((book) => {
        return(
                    <Container key={book.id} className='d-flex rounded my-1 ml-0 p-1 bg-white book-result' style={{width: '15rem', marginLeft: '0', alignItems: 'start'}}>
                        <Card.Img src={book.volumeInfo.imageLinks !== undefined ? book.volumeInfo.imageLinks.smallThumbnail : ""} style={{width: '2rem', aspectRatio: '2/3'}}></Card.Img>
                        <Container onClick={() => {
                            setModalData(book);
                            setShowModal(true);
                        }}>
                            <Card.Title style={{fontSize: '12pt'}}>{book.volumeInfo.title}</Card.Title>
                            <Card.Subtitle className='author-name' style={{fontSize: '10pt'}}>{book.volumeInfo.authors !== undefined ? "by "+book.volumeInfo.authors[0] : "No author"}</Card.Subtitle>  

                        </Container>
                    </Container>
        )
    }) : null;


    return(
        <Container fluid className='d-flex bg-primary mx-0 mb-1 p-0 bar overlap' style={{height: '3rem', position: 'fixed', top: '0'}}>
            <div className="px-2 help text-light" onClick={() => setShowHelp(true)}>
                <h2>?</h2>
            </div>
            <HelpModal show={showHelp} onHide={setShowHelp} />
            <form onSubmit={handleSubmit} className='mt-1' style={{textAlign: 'left'}}>
                <Container className='px-0' id="searchField">
                    <input
                        type="text"
                        placeholder="Search books..."
                        name="term"
                        value={search.term}
                        onChange={handleChange}
                        id='termForm'
                        className="mt-1 bg-primary text-white"
                        style={{width: '15rem', marginBottom: '0', border: '0'}}
                    />
                    {!hidden && bookResponse ? <Container className='px-0' style={{zIndex: '1'}}>
                        {cardArr.slice(0,4)}
                    </Container> : ""}
                </Container>
            </form>
            <BookModal show={showModal} onHide={() => {
                setShowModal(false);
                setShowAddBook(false);
            }} showAddBook={showAddBook} book={modalData} changeModalBook={setModalData} addBook={addBook} setShowAddBook={setShowAddBook}/>
        </Container>
    );
}