import React from "react";
import ModalEditionImagesContainer from "./components/ModalEditionImagesContainer";
import { Container, Row, Modal, Button } from "react-bootstrap";

export default function BookModal({ show, onHide, addBook, showAddBook, setShowAddBook, changeModalBook, book}){
    return(
        <Modal show={show} onHide={onHide} size='xl'>
        <Modal.Header>
            <Modal.Title>{book.volumeInfo.title}</Modal.Title>
            <Modal.Title>{book.volumeInfo.authors[0]}</Modal.Title>
        </Modal.Header>
        <Container className='d-flex pb-5' style={{alignItems: 'start'}}>
            <div className='d-flex flex-column align-items-stretch'>
                <img alt={book.volumeInfo.title} src={book.volumeInfo.imageLinks !== undefined ? book.volumeInfo.imageLinks.thumbnail : ""} style={{width: '12rem', aspectRatio: '2/3'}}/>
                <p className="fw-light">Published: <i>{book.volumeInfo.publisher}</i>, {book.volumeInfo.publishedDate}</p>
                {book.volumeInfo.pageCount > 1 ? <p>{book.volumeInfo.pageCount} pages</p> : ""}
                {book.volumeInfo.industryIdentifiers &&
                 ((book.volumeInfo.industryIdentifiers[0] && JSON.parse(localStorage.getItem(book.volumeInfo.industryIdentifiers[0].identifier)))
                  || (book.volumeInfo.industryIdentifiers[1] && JSON.parse(localStorage.getItem(book.volumeInfo.industryIdentifiers[1].identifier) ))) ?
                   <h2>In library</h2> :
                  <div>
                        <Button className="mt-2" style={{width: '12rem'}}
                    onClick={() => setShowAddBook(true)}>
                        Add to library
                        </Button>
                        { showAddBook ? <Container>
                            <Row>
                                <div className="text-center border add-book-box" onClick={() => addBook({category: "tbr", isbn: book.volumeInfo.industryIdentifiers[0].identifier})}><h5>To-Read</h5></div>
                            </Row>
                            <Row>
                                <div className="text-center border add-book-box" onClick={() => addBook({category: "read", isbn: book.volumeInfo.industryIdentifiers[0].identifier})}><h5>Read</h5></div>
                            </Row>
                            <Row>
                                <div className="text-center border add-book-box" onClick={() => addBook({category: "cr", isbn: book.volumeInfo.industryIdentifiers[0].identifier})}><h5>Currently Reading</h5></div>
                            </Row>
                        </Container> : undefined

                        }
                  </div>
                }
            </div>
            <Modal.Body style={{width: '40rem', paddingTop: '0'}}>{book.volumeInfo.description}</Modal.Body>
        </Container>
        <Container>
            <ModalEditionImagesContainer changeModalBook={changeModalBook} otherEditions={book.otherISBNs} />
        </Container>
    </Modal>
    )
}