import React from "react";
import { Card, CloseButton } from "react-bootstrap/esm";

export default function HoverCloseCard({ img, isbns, showBookPanel, selected }){
    const [isHovered, setIsHovered] = React.useState(false);

    const isbn = () => {
        if(isbns.length > 1){
            if(isbns[0].type === "ISBN_13") return isbns[0].identifier;
            else return isbns[1].identifier;
        } else if(isbn[1]){
            return isbn[1].identifier;
        }
    }

    let style;
    if(selected && selected === isbn()){
        style = { width: '8rem', aspectRatio: '2/3', flex: '1', objectFit: 'cover', opacity: '50%' }
    } else {
        style = { width: '8rem', aspectRatio: '2/3', flex: '1', objectFit: 'cover' }
    }

    return(
        <Card className="mb-0 mx-1 d-inline-block bg-dark book-cover close-card" 
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        >
                <CloseButton hidden={!isHovered} onClick={() => {
                    localStorage.removeItem(isbn());
                }}
                className='btn-close-white'></CloseButton>
                <Card.Img 
                    src={img} 
                    style={style}
                    onClick={() => showBookPanel(isbns)}
                />
        </Card>
    );

}