class Pair {
    constructor(key, value){
        this.key = key;
        this.value = value;
    }
}

export class BooksHashMap {
    // [[[Pairs], [Pairs], [Pairs]], [[Pairs]], [[Pairs]], [[Pairs]]]
    // Pair: str key = book's title + book's authors (i.e. "ThePenelopiadMargaretAtwood")
    //       Obj value = JSON object representing book metadata


    constructor(capacity){
        this.capacity = capacity;
        this.map = Array.apply(null, Array(capacity)).map(function () {});
        this.size = 0;
        this.loadFactor = 1.00;
    }
    
    /**
     * 
     * @param {string} key 
     * @returns 
     */
    _hash(key){
        let hash = 0;
        for(let i = 0; i < key.length; i++){
          hash += key.charCodeAt(i);
        }
        return hash % this.capacity;
    }

    /**
     * 
     * @param {string} key 
     */
    has(key){
        // this.map = [[[], [], []], [], []] = [hashIndex, other, other]
            // this.map[hashIndex] = [[], [], []] -- each array represents a unique book
            // this.map[hashIndex][0] = [Pair, Pair, Pair]
            // this.map[hashIndex][i][0] = Pair
        let hashString = this.removeWhitespace(key);
        let hashIndex = this._hash(hashString);
        if(this.map === undefined) return false;
        if(this.map[hashIndex] === undefined) return false;
        else {
            for(let i = 0; i < this.map[hashIndex].length; i++){
                for(let j = 0; j < this.map[hashIndex][i].length; j++){
                    if(this.map[hashIndex][i][j].key === hashString) return true;
                }
            }
        }
        return false;
    }

    /**
     * 
     * @param {*} obj JSON object representing a given book
     */
    put(obj){
        let hashString = "";
        if(obj.volumeInfo.title) hashString += obj.volumeInfo.title.trim();
        if(obj.volumeInfo.authors) {
            for(let i = 0; i < obj.volumeInfo.authors.length; i++){
                hashString += obj.volumeInfo.authors[i].trim();
            }
        }
        hashString = this.removeWhitespace(hashString);
        let newPair = new Pair(hashString, obj);
        let hashIndex = this._hash(hashString);

        if(((this.size + 1)/this.capacity) >= this.loadFactor){
            this.capacity = this.capacity * 2;
            let arr = Array.apply(null, Array(this.capacity)).map(function () {});
            for(let i = 0; i < this.map.length; i++){
                // this.map[i] = [[Pairs], [Pair], [Pair]]
                // this.map[i][j] = [Pairs] = [Pair, Pair, Pair] = one unique book
                    // instead of iterating through this, take key of first Pair (this.map[i][j][0].key), rehash, 
                    // and insert this.map[i][j] at new rehashed index

                if(this.map[i] !== undefined){
                    for(let j = 0; j < this.map[i].length; j++){
                        let newHash = this._hash(this.map[i][j][0].key);

                        if(arr[newHash] === undefined){
                            arr[newHash] = [];
                            arr[newHash].push(this.map[i][j]);
                        } else {
                            arr[newHash].push(this.map[i][j]);
                        }
                    }
                }
            }
            this.map = arr;
        }
        if(this.map[hashIndex] === undefined){
            this.map[hashIndex] = [];
            this.map[hashIndex].push([newPair]);
        }
        else {
            let bookDup = false;
            //iterate through all elements in current 
            // this.map = [[[], [], []], [], []] = [hashIndex, other, other]
            // this.map[hashIndex] = [[], [], []] -- each array represents a unique book
            // this.map[hashIndex][0] = [Pair, Pair, Pair]
            // this.map[hashIndex][i][0] = Pair

            // Iterate through all arrays at hashIndex to see if there exists a book that equals obj
            for(let i = 0; i < this.map[hashIndex].length; i++){
                if(this.map[hashIndex][i][0] && this.map[hashIndex][i][0].value.volumeInfo){
                    let otherBook = this.map[hashIndex][i][0].value.volumeInfo;
                    let otherTitle = otherBook.title;
                    let otherAuthors = otherBook.authors;
                    if(otherTitle.toLowerCase() === obj.volumeInfo.title.toLowerCase()){
                        if(otherAuthors !== null && otherAuthors !== undefined) {
                            // if obj is the same book as otherBook, push to same array as otherBook
                            if(otherAuthors.includes(obj.volumeInfo.authors[0])) {
                                this.map[hashIndex][i].push(newPair);
                                bookDup = true;
                                break;
                            }
                        }
                    }
                } 
            }
            if(!bookDup) this.map[hashIndex].push([newPair]);
        }
        this.size++;

    }

    /**
     * 
     * @param {String} key String containing the title and first author of a book, in that order.
     *                 Key's should be in the form "TitleAuthor" and contain minimal whitespace
     * @returns {Array} Array of Pair objects that contain a book's metadata in the Pair.value field
     */
    get(key){
        // this.map = [[[], [], []], [], []] = [hashIndex, other, other]
        // this.map[hashIndex] = [[], [], []] -- each array represents a unique book
        // this.map[hashIndex][0] = [Pair, Pair, Pair]
        // this.map[hashIndex][i][0] = Pair
        let hashString = this.removeWhitespace(key);
        let hashIndex = this._hash(hashString);
        if(!this.has(key)){
            return null;
        }
        if(this.map[hashIndex] === undefined) return null;
        let bucket = this.map[hashIndex];
        for(let i = 0; i < bucket.length; i++){
            for(let j = 0; j < bucket[i].length; j++){
                if(bucket[i][0].key === hashString) return bucket[i];
            }
        }
        return null;
    }

    clear() {
        this.map = Array.apply(null, Array(this.capacity)).map(function () {});
        this.size = 0;
    }

    /**
     * 
     * @param {String} str 
     * @returns 
     */
    removeWhitespace(str){
        return str.replaceAll(" ", "").trim().toLowerCase();
    }
    
    // TODO: add sort by relevance
    convertToJSONArray(){
        let arr = []
        this.map.filter(arr => arr !== undefined)
        .map(pairArr => {
            // pairArr = [Pair, Pair, Pair]
            pairArr.map(bookArr => {
                let isbns = [];
                if(bookArr[0].value.volumeInfo.industryIdentifiers){
                    if(bookArr.length > 1){
                    for(let i = 1; i < bookArr.length; i++){
                        if(bookArr[i].value.volumeInfo.industryIdentifiers){
                            isbns.push(bookArr[i].value.volumeInfo.industryIdentifiers[0].identifier);
                        }
                    }
                }
                }
                bookArr[0].value.otherISBNs = isbns;
                arr.push(bookArr[0].value);
            })
        })
        return arr;
    }
    // insertion sort on this.map.filter(ele => ele !== undefined)
    insertionSort(arr)  
{  
    let i, key, j;  
    for (i = 1; i < arr.length; i++) 
    {  
        key = arr[i];  
        j = i - 1;  
   
        /* Move elements of arr[0..i-1], that are  
        greater than key, to one position ahead  
        of their current position */
        while (j >= 0 && arr[j].otherISBNs.length < key.otherISBNs.length) 
        {  
            arr[j + 1] = arr[j];  
            j = j - 1;  
        }  
        arr[j + 1] = key;  
    } 
    return arr; 
} 

    findMaxLength(arr){
        let maxLength = 0;
        for(let i = 0; i < arr.length; i++){
            if(arr[i].length > maxLength) maxLength = arr[i].length;
        }
        return maxLength;
    }

}


class TestBooksHashMap {

    constructor(){
        this.book1 = {
            volumeInfo: {
                title: "The Penelopiad",
                authors: [
                    "Margaret Atwood"
                ],
                industryIdentifiers: [
                    {type: "ISBN_13", identifer: "98765432Y"}
                ]
            }
        }
        
        this.book2 = {
            volumeInfo: {
                title: "The Penelopiad",
                authors: [
                    "Margaret Atwood"
                ],
                industryIdentifiers: [
                    {type: "ISBN_13", identifer: "1234567X"}
                ]
            }
        }
        
        this.book3 = {
            volumeInfo: {
                title: "No Longer Human",
                authors: [
                    "Osamu Dazai"
                ],
                industryIdentifiers: [
                    {type: "ISBN_13", identifer: "83671953Z"}
                ]
            }
        }
        
        this.book4 = {
            volumeInfo: {
                title: "The Flowers of Buffoonery",
                authors: [
                    "Osamu Dazai"
                ],
                industryIdentifiers: [
                    {type: "ISBN_13", identifer: "987329500"}
                ]
            }
        }
    }

    testPopulatingHash(){
        let hm = new BooksHashMap(25);
        hm.put(this.book1);
        hm.put(this.book2);
        hm.put(this.book3);
        if(hm.size !== 3) return false;
        if(!hm.has("The Penelopiad Margaret Atwood")) return false;
        if(!hm.has("No Longer Human Osamu Dazai")) return false;
        return true;
    }

    testRetrievingBooks(){
        let hm = new BooksHashMap(32);
        hm.put(this.book3);
        hm.put(this.book4);
        let nlh = hm.get("No Longer Human Osamu Dazai");
        let fob = hm.get("The Flowers of Buffoonery Osamu Dazai");
        let na = hm.get("Not a book");
        if(na !== null) return false;
        if(fob[0].value.volumeInfo.title !== "The Flowers of Buffoonery"){
            console.log("Error: Expected 'The Flowers of Buffoonery', got " + fob[0].value.volumeInfo.title);
            return false;
        }
        if(nlh[0].value.volumeInfo.title !== "No Longer Human"){
            console.log("Error: Expected 'No Longer Human', got " + nlh[0].value.volumeInfo.title);
            return false;
        }

        hm.put(this.book1);
        hm.put(this.book2);
        let tp = hm.get("The Penelopiad Margaret Atwood");
        if(tp.length < 2){
            console.log("Error: Expected length of 2, got " + tp.length);
            return false;
        }
        try {
            if(tp[1].value.volumeInfo.title !== "The Penelopiad"){
                console.log("Error: Expected 'The Penelopiad', got " + tp[1].value.volumeInfo.title);
                return false;
            }
        } catch (error) {
            console.log("Accessing second element in array resulted in error being thrown");
            return false;
        }

        return true;
    }

    testFindBook(){
        let hm = new BooksHashMap(32);
        hm.put(this.book1);
        hm.put(this.book3);
        hm.put(this.book4);
        
        if(!hm.has("The Penelopiad Margaret Atwood")) return false;
        if(!hm.has("The Flowers of Buffoonery Osamu Dazai")) return false;
        if(hm.has("Not in list")) return false;

        return true;
    }

    testConvert(){
        let hm = new BooksHashMap(32);
        hm.put(this.book1);
        hm.put(this.book2);
        hm.put(this.book3);
        hm.put(this.book4);
        console.log(hm.convertToJSONArray());
    }

    runAll(){
        console.log("testPopulatingHash? " + this.testPopulatingHash());
        console.log("testRetrievingBooks? " + this.testRetrievingBooks());
        console.log("testFindBook? " + this.testFindBook());
    }

}