import React from 'react'
// import { addDoc } from 'firebase/firestore'
// import { db, books } from '../firebase'
// import { onSnapshot } from 'firebase/firestore'
import { Container, Row, Col } from 'react-bootstrap'
import Library from './Library'
import BookInfoPanel from './BookInfoPanel'
import './styles/home.scss'
import SearchBar from './SearchBar'

export default function Home(){
    function loadDataFromLocalStorage() {
        const localStorageData = {};
        for (let i = 0; i < localStorage.length; i++) {
          const key = localStorage.key(i);
          if(key !== "debug" && key !== "loaded"){
            const value = localStorage.getItem(key);
            localStorageData[key] = JSON.parse(value);
          }
        }
        return localStorageData;
      }

    function processLocalStorageData() {
        const localStorageData = loadDataFromLocalStorage();
        
        const full = [];
        const favorites = [];
        const read = [];
        const tbr = [];
        const cr = [];

        Object.entries(localStorageData).forEach(([key, value]) => {
            // Check boolean fields and update corresponding arrays
            full.push(key);
            if (value.favorites) favorites.push(key);
            if (value.read) read.push(key);
            if (value.tbr) tbr.push(key);
            if (value.cr) cr.push(key);
          });
          
          return {
            full: full,
            favorites: favorites,
            read: read,
            tbr: tbr,
            cr: cr
          };
    }


    const [lib, setLib] = React.useState(processLocalStorageData())
    const [showRight, setShowRight] = React.useState(false)
    const [bookInfo, setBookInfo] = React.useState({})
    const [selected, setSelected] = React.useState("")

    React.useEffect(() => {
        setLib(processLocalStorageData());
    }, [])

    function addBook(book){
        if(book.category === "tbr"){
            setLib(prevLib => {
                return {
                    ...prevLib,
                    full: [...prevLib.full, book.isbn],
                    tbr: [...prevLib.tbr, book.isbn]
                }
            })
        } else if(book.category === "read"){
            setLib(prevLib => {
                return {
                    ...prevLib,
                    full: [...prevLib.full, book.isbn],
                    read: [...prevLib.read, book.isbn]
                }
            })
        } else {
            setLib(prevLib => {
                return {
                    ...prevLib,
                    full: [...prevLib.full, book.isbn],
                    cr: [...prevLib.cr, book.isbn]
                }
            })
        }
    }

    function handleBookClick(isbns){
        if(selected === isbns[0].identifier || selected === isbns[1].identifier){
            setSelected("");
            setShowRight(false);
        }
        else {
            if(JSON.parse(localStorage.getItem(isbns[0].identifier)) || JSON.parse(localStorage.getItem(isbns[1].identifier))){
                if(JSON.parse(localStorage.getItem(isbns[0].identifier))){
                    setBookInfo(JSON.parse(localStorage.getItem(isbns[0].identifier)));
                    setSelected(isbns[0].identifier);
                }
                else {
                    setBookInfo(JSON.parse(localStorage.getItem(isbns[1].identifier)));
                    setSelected(isbns[1].identifier);
                }
            }
            if(!showRight) setShowRight(true);
        }
    }

    return(
            <main className='p-0 mt-1'>
                <SearchBar style={{float: 'right'}} addBook={addBook}/>

                <Container>
                    <Row>
                        <Col>
                            <Library lib={lib} selected={selected} showBookPanel={handleBookClick}/>
                        </Col>
                        {showRight ? <Col>
                                <BookInfoPanel bookInfo={bookInfo}/>
                            </Col> : null}
                    </Row>
                </Container>
            </main>
    );
}

